import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			nickname: '',
			contact: '',
			mobile: '',
			password: '',
			address: '',
			details: '',
			longitude:'',
			latitude:'',
			hide:true,
			count:0
		};
	},
	methods: {
		leftClick() {
			this.hide = true;






			uni.navigateBack({
				delta:1
			})

		},

		mapPage() {

















			let that = this;
			uni.chooseLocation({
				success: function(res) {
					that.address = res.address;
					var obj = that.tobdMap(res.longitude, res.latitude);
					that.lng = obj.lng;
					that.lat = obj.lat;
				},
				fail: function(err) {
					console.log(err)
				}
			});
		},
		tobdMap(lng, lat) {
			let x_pi = 3.14159265358979324 * 3000.0 / 180.0;
			let x = lng;
			let y = lat;
			let z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * x_pi);
			let theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * x_pi);
			let lngs = z * Math.cos(theta) + 0.0065;
			let lats = z * Math.sin(theta) + 0.006;
		
			return {
				lng: lngs,
				lat: lats
			}
		},
		formSubmit() {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0
			}, 1000)
			var that = this;
			var nickname = that.nickname;
			var contact = that.contact;
			var mobile = that.mobile;
			var password = that.password;
			var address = that.address;
			var longitude = that.longitude;
			var latitude = that.latitude;
			var details = that.details;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
			  address: address,
			  appid: appid,
			  contact: contact,
			  mobile: mobile,
			  nickname: nickname,
			  password: password,
			  timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			if (nickname == "") {
			  rs.Toast("名称不能为空")
			  return false;
			}
			if (contact == "") {
			  rs.Toast("联系人不能为空")
			  return false;
			}
			if (mobile == "") {
			  rs.Toast("手机号不能为空")
			  return false;
			}
			if (password.length < 6) {
			  rs.Toast("密码不能小于六位数")
			  return false;
			}
			
			var data = {
			  address: address,
			  appid: appid,
			  contact: contact,
			  mobile: mobile,
			  nickname: nickname,
			  password: password,
			  timeStamp: timeStamp,
			  sign: sign,
			  longitude: longitude,
			  latitude: latitude,
			  details: details
			}
			rs.postRequests("addChild", data, (res) => {
			  if (res.data.code == 200) {
			    rs.Toast('添加成功')
			    setTimeout(function() {
			    	uni.navigateTo({
			    		url: "accountmange"
			    	})
			    }, 1000);
			  }else{
			    rs.Toast(res.data.msg)
			  }
			})
		}
	},
	onLoad(options) {
		var data = uni.getStorageSync('append');
		console.log(uni.getStorageSync('append'))
		if(data){
			this.nickname = data.childInfo;
			this.contact = data.contact;
			this.mobile = data.mobile;
			this.password = data.password;
			this.address = data.address;
			this.details = data.details;
		}
		this.latitude = options.lat;
		this.longitude = options.lng;
	},
	onHide(){
		if(this.hide == true){
			uni.removeStorage({ key: 'append'})
		}
	}
};
